import { Component, OnInit, Input, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-landing-testimonials',
  templateUrl: './landing-testimonials.component.html',
  styleUrls: ['./landing-testimonials.component.scss']
})
export class LandingTestimonialsComponent implements OnInit {

	@Input() runNumber 	: boolean
	@Input() mobile 	: boolean

	config2: Object = {
		direction             : 'horizontal',
		speed                 : 400,
		autoplay              : 8000,
		spaceBetween          : 0,
		loop                  : true,
		pagination            : '.swiper-pagination',
		paginationClickable   : true,
		paginationHide        : false,
	}

	countFrom 	: number
	countTo 	: number = 0
	countTo2 	: number = 0
	countTo3 	: number = 0

	constructor() { }

	ngOnInit() {
		this.countTo 	= 10894;
		this.countTo2 	= 93078;
		this.countTo3 	= 34293;
	}

	// ngOnChanges(changes: SimpleChanges) {
	// 	if (changes.runNumber.currentValue) {
	// 		this.countFrom = 0;
	// 	}
	// }

	numberWithComma(x) {
		return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
	}

}



