import { Component, OnInit, OnDestroy, Input, HostListener, Inject } from '@angular/core';
import { DOCUMENT } from "@angular/platform-browser";
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';

/* Services */
import { AccountService } from '../../../Services/account.service';

/* Animation */
import { wordRotate } from '../../../Animations/rotate-word.animation';

export const states = ['initial', 'inline', 'outDown']

@Component({
	selector: 'app-landing-post',
	templateUrl: './landing-post.component.html',
	styleUrls: ['./landing-post.component.scss'],
	animations: [wordRotate]
})
export class LandingPostComponent implements OnInit, OnDestroy {

	@Input() mobile : boolean

	hideWord 	: string
	hideWord2 	: string
	topPixelStr : string
	topPixelStr2: string

	state1 		: string = 'initial'
	state2 		: string = 'inLine'
	state3		: string = 'inLine'
	state4 		: string = 'inLine'

	state2_1 	: string = 'initial'
	state2_2 	: string = 'inLine'
	state2_3	: string = 'inLine'
	state2_4 	: string = 'inLine'

	count 		: number = 0
	count2 		: number = 0
	topCount 	: number = 0
	topCount2 	: number = 0
	startY 		: number = 0
	initScroll  : number = 0

	interval 	: any
	interval2 	: any

	constructor(private AccountService: AccountService,
				private dialogService: DialogService,
				@Inject(DOCUMENT) private document: any) {
		this.wordChange()
		// setTimeout(() => {
			// this.wordChange2();
		// }, 2000)

	}

	ngOnInit() {
		this.getState(0);
		this.getState2(0);
	}

	ngOnDestroy() {

	}

	@HostListener("window:scroll", [])
	onWindowScroll() {
		if (this.initScroll === 0) {
			this.initScroll = 1
			this.startY = window.pageYOffset
		}
		if (this.startY <= window.pageYOffset) {
			this.topPixelStr 	= this.topCount-- + 'px';
			this.topPixelStr2 	= (this.topCount2 -= 2) + 'px'
			this.startY 		= window.pageYOffset
		} else {
			this.topPixelStr 	= this.topCount++ + 'px';
			this.topPixelStr2	= (this.topCount2 += 2) + 'px';
			this.startY 		= window.pageYOffset
		}
	}

	wordChange() {
		this.interval = setInterval( () => {
			this.count++;
			this.count2++;
			if (this.count === 4)
				this.count = 0
			if (this.count2 === 4)
				this.count2 = 0
			this.getState(this.count);
			setTimeout(() => {
				this.getState2(this.count2)
			}, 2000);
		}, 4000);
	}

	getState(count) {
		if (!count) {
			this.state1 	= states[0]; // Initial
			this.state2 	= states[1]; // Inline
			this.state3 	= states[1]; // Inline
			this.state4 	= states[2]; // OutDown
			this.timeOut(1, 'idea');
		} else if (count === 1) {
			this.state1 	= states[2]; // OutDown
			this.state2 	= states[0]; // Initial
			this.state3 	= states[1]; // Inline
			this.state4 	= states[1]; // Inline
			this.timeOut(1, 'project');
		} else if (count === 2) {
			this.state1 	= states[1]; // Inline
			this.state2 	= states[2]; // OutDown
			this.state3 	= states[0]; // Initial
			this.state4 	= states[1]; // Inline
			this.timeOut(1, 'startup');
		} else if (count === 3) {
			this.state1 	= states[1]; // Inline
			this.state2 	= states[1]; // Inline
			this.state3 	= states[2]; // OutDown
			this.state4 	= states[0]; // Initial
			this.timeOut(1, 'product');
		}
	}

	getState2(count) {
		if (!count) {
			this.state2_1 	= states[0]; // Initial
			this.state2_2 	= states[1]; // Inline
			this.state2_3 	= states[1]; // Inline
			this.state2_4 	= states[2]; // OutDown
			this.timeOut(2, 'cofounder');
		} else if (count === 1) {
			this.state2_1 	= states[2]; // OutDown
			this.state2_2 	= states[0]; // Initial
			this.state2_3 	= states[1]; // Inline
			this.state2_4 	= states[1]; // Inline
			this.timeOut(2, 'early adopters');
		} else if (count === 2) {
			// this.state2_1 	= states[1]; // Inline
			this.state2_2 	= states[2]; // OutDown
			this.state2_3 	= states[0]; // Initial
			this.state2_4 	= states[1]; // Inline
			this.timeOut(2, 'teammate');
		} else if (count === 3) {
			this.state2_1 	= states[1]; // Inline
			this.state2_2 	= states[1]; // Inline
			this.state2_3 	= states[2]; // OutDown
			this.state2_4 	= states[0]; // Initial
			this.timeOut(2, 'coworkers');
		}
	}

	timeOut(index, word) {
		if (index === 1)
			this.hideWord  = word
		else
			this.hideWord2 = word
	}

	/* SHOW MODAL */
	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

	// getSuggestion() {
	// 	this.AccountService.suggestProfile({projectId: 474}).subscribe(res => {
	// 		console.log(res);
	// 	});
	// 	this.AccountService.suggestProject({userId: 5541}).subscribe(res => {
	// 		console.log(res);
	// 	});
	// }
}



