import { trigger, state, animate, style, transition, keyframes, AnimationEntryMetadata } from '@angular/core';

/* Slide Right/Left dialog */
export const wordRotate = trigger('wRotate', [
			state('initial', style({
				position: 'absolute', opacity: 1, top: '0px'
			})),
	    	state('outDown', style({
	    		position: 'absolute', opacity: 0, top: '15px'
	    	})),
	    	state('inLine', style({
	    		position: 'absolute', opacity: 0, top: '0px'
	    	})),
	    	// state('inLeft', style({
	    	// 	opacity: 1, left: '0px', right: '0px'
	    	// })),
	    	// state('outRight', style({
	    	// 	opacity: 0, right: '-180px', left: '0px',
	    	// })),
	    	// state('inRight', style({
	    	// 	opacity: 1, right: '0px', left: '0px',
	    	// })),
	    	transition('initial => outDown', animate('150ms')),
	    	transition('outDown => inLine', animate('0ms')),
	    	transition('inLine => initial', animate('400ms')),
			// transition('initial => outLeft', animate('250ms')),
			// transition('outRight => initial', animate('250ms 200ms')),
			// transition('initial => outRight', animate('250ms'))
	    ]);



