import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';

/* Components */

/* Directives */

/* Services */
import { AccountService } from '../../Services/account.service'
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { ProjectsService } from '../../Services/Projects/projects.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';

/* Library */
import { NgxSiemaModule } from 'ngx-siema';
import { SwiperModule, SwiperConfigInterface } from 'ngx-swiper-wrapper';
// import { CountoModule }  from 'angular2-counto';

/* Route */
import { LANDING_ROUTES } from '../../Routes/landing.routes';
import { LandingComponent } from './landing.component';
import { LandingPostComponent } from './landing-post/landing-post.component';
import { LandingProjectsComponent } from './landing-projects/landing-projects.component';
import { LandingPeopleComponent } from './landing-people/landing-people.component';
import { LandingSubscribeComponent } from './landing-subscribe/landing-subscribe.component';
import { LandingTestimonialsComponent } from './landing-testimonials/landing-testimonials.component';
import { BackofficeService } from '../../Services/Backoffice/backoffice.service';

const SWIPER_CONFIG: SwiperConfigInterface = {
    direction: 'horizontal',
    slidesPerView: 'auto',
    keyboardControl: true,
    speed: 300,
};


@NgModule({
	imports: [
		LANDING_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		FooterModule,
		PipeModule,
		NgxSiemaModule.forRoot(),
		SwiperModule.forRoot(SWIPER_CONFIG),
		// CountoModule
	],
	exports: [
	],
	declarations: [
		LandingComponent,
		LandingPostComponent,
		LandingProjectsComponent,
		LandingPeopleComponent,
		LandingSubscribeComponent,
		LandingTestimonialsComponent
	],
	providers: [
		AccountService,
		PicturesService,
		ProjectsService,
		ProfilesService,
		BackofficeService
	]
})
export class LandingModule { }



